import React from "react";
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";
import adminApi from "../../../../api/adminApi";

const BoxForgot = (props) => {
  const form = useForm();

  const handleForgot = async (values) => {
    try {
      const data = {};
      data.email = values.email;
      console.log(values);
      await adminApi.SendMail(data);
      Swal.fire({
        title: " gi m xc thc vo email ca bn",
        text: "Bn c mun chuyn n trang Email ca mnh  xc thc ti khon khng ?",
        icon: "success",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "chuyn n Email",
        cancelButtonText: " li trang ny",
      }).then((result) => {
        if (result.isConfirmed) {
          window.location = "https://mail.google.com/mail/u/0/";
        }
      });
      // Swal.fire(' gi m xc thc', `vui lng vo hp mail ca mnh  xc thc ti khon `, 'success');
    } catch (err) {
      Swal.fire("Email khng tn ti", "error");
      console.log(err);
    }
  };

  return (
    <div className="user signupBx">
      <div className="formBx">
        <form onSubmit={form.handleSubmit(handleForgot)}>
          <h2>Qun Mt Khu</h2>
          <input
            type="email"
            name="email"
            id
            placeholder="Nhp Email"
            ref={form.register({ required: true })}
          />
          {form.errors.email && <p className="CatchError">* Vui lng nhp email</p>}
          <button type="submit">Ly Li Mt Khu</button>
          <p className="signup">
            Bn  C Ti Khon ?{" "}
            <span onClick={props.toggleForm}>ng Nhp.</span>
          </p>
        </form>
      </div>
      <div className="imgBx">
        <img src={props.logout} alt="" />
      </div>
    </div>
  );
};

export default BoxForgot;
