import React, { useEffect, useState } from "react";
import { PageHeader, Row, Col, Button } from "antd";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import vipApi from "../../../../api/vipApi";
import ListVip from "./Components/ListVip";
import FormVipAdd from "./Components/FormVipAdd";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useForm } from "react-hook-form";
import InputField from "../../Components/Common/FromControl/InputField";
const Vip = () => {
  const [demo, setdemo] = useState("");
  const [DataVip, setDataVip] = useState([]);
  const schema = yup
  .object({
    name: yup.string().required("Vui lng nhp trng ny"),
    price: yup.string().required("Vui lng nhp trng ny"),
    time: yup.string().required("Vui lng nhp trng ny"),
  })
  .required();
  const forms = useForm({
    defaultValues: {
        name: '',
        price: '',
        time: '',
    },
    resolver: yupResolver(schema),
});
  let history = useHistory();

  const handleSubmitFrom = (values) => {
    const fetchUpdateVip = async (data) => {
      try {
        const res = await vipApi.AddVip(data);
        if (res.status === 200) {
          Swal.fire("Thm", "Thm Thnh Cng!", "success").then((result) => {
            if (result.isConfirmed) {
              setdemo((pre) => pre + 1);
              history.push({ pathname: "/admin/vip" });
            }
          });
        }
        if (res.data.code === 404) {
            Swal.fire(
              "Khng th thm",
              "Dch v ny  c trong danh sch",
              "error"
            )
           
          }
      } catch (err) {
        Swal.fire(
          "Khng th thm",
          "M dch v  c trong danh sch",
          "error"
        );
      }
    };

    fetchUpdateVip(values);
  };

  const handleRemove = (id) => {
    try {
      const fetchRemoveVip = async (data) => {
        try {
          const res = await vipApi.DelVip(data);
          if (res.status === 200) {
            Swal.fire("Xa", "Xa Thnh Cng!", "success").then((result) => {
              if (result.isConfirmed) {
                console.log(1);
                setdemo((pre) => pre + 1);
                history.push({ pathname: "/admin/vip" });
              }
            });
          }
          
        } catch (err) {
          console.log(err);
          Swal.fire("Li", "Khng  Thm quyn  xa", "error").then(
            (result) => {
              if (result.isConfirmed) {
                setdemo((pre) => pre + 1);
                history.push({ pathname: "/admin/vip" });
              }
            }
          );
        }
      };

      fetchRemoveVip(id);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    const fetchVip = async () => {
      const res = await vipApi.GetVip();
      setDataVip(res);
    };

    fetchVip();
  }, [demo]);

  return (
    <div className="CatalogsPage">
      <PageHeader className="site-page-header" title="Trang Gi VIP" />

      <div className="BoxForm">
        <div className="title">Thm Gi VIP</div>
        <form onSubmit={forms.handleSubmit(handleSubmitFrom)}>

            <div className="GroupForm">
                <label htmlFor="name">Tn Gi VIP</label>
                <InputField name="name" type='text' form={forms}></InputField>
                {forms.errors.name && (
              <p className="CatchError">* Vui lng nhp trng ny</p>
            )}
            </div>
            <div className="GroupForm">
                <label htmlFor="price">Gi Gi</label>
                <InputField name="price" type='text' form={forms} ></InputField>
                {forms.errors.price && (
              <p className="CatchError">* Vui lng nhp trng ny</p>
            )}
            </div>
            <div className="GroupForm">
                <label htmlFor="time">Thi gian ht hn gi</label>
                <InputField name="time" type='number' form={forms}></InputField>
                {forms.errors.time && (
              <p className="CatchError">* Vui lng nhp trng ny</p>
            )}
            </div>
            <Button htmlType='submit'>Lu</Button>
        </form>
      </div>

      <Row className="ListCata">
        <Col span={24}>
          <ListVip data={DataVip.data} handleRemove={handleRemove} />
        </Col>
      </Row>
    </div>
  );
};

export default Vip;
