import { Button, Table, Tag } from "antd";
import React from "react";
import { Link } from "react-router-dom";

const ListCoupon = (props) => {
  const columns = [
    {
      title: "M gim",
      dataIndex: "code",
      key: "code",
    },
    {
      title: "S tin gim",
      key: "percent",
      render: (record) => {
        return (
          <>
            {record.percent.toLocaleString("vi", {
              style: "currency",
              currency: "VND",
            })}
          </>
        );
      },
    },
    {
      title: "Trng thi",
      key: "status",
      render: (record) => {
        return (
          <>
            {record.status === true ? (
              <Tag color="success">Coupon  c gi</Tag>
            ) : (
              <Tag color="processing">Coupon cha c gi</Tag>
            )}
          </>
        );
      },
    },
    {
      title: "Sa",
      key: "edit",
      render: (record) => {
        return (
          <>
            <Button>
              <Link to={`/admin/coupon/edit/${record._id}`}>Sa</Link>
            </Button>
          </>
        );
      },
    },
    {
      title: "Xa",
      key: "del",
      render: (record) => (
        <Button onClick={() => props.handleRemove(record._id)}>Xa</Button>
      ),
    },
  ];
  return (
    <>
      <Table
        className="ListCataTable"
        dataSource={props.data}
        columns={columns}
      />
    </>
  );
};

export default ListCoupon;
