import { Button, Col, Input, PageHeader, Row, Select, Table } from 'antd';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useHistory, useRouteMatch } from 'react-router';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import ChapterApi from '../../../../../../api/chapterApi';

const { Option } = Select;


const Chapter = (props) => {

    const match = useRouteMatch();
    let history = useHistory();
    const [DataChap, setDataChap] = useState();
    const [demo, setdemo] = useState('')


    const forms = useForm({
        defaultValues: {
            nameChapter: "",
            stt: "",
        },
    });

    useEffect(() => {
        const idProduct = match.params.id;
        const fetchCataByID = async (idCata) => {
            const res = await ChapterApi.GetChapterByIdProduct(idProduct)
            return setDataChap(res.data);
        }

        fetchCataByID(idProduct)
    }, [demo])

    const handleSubmitFrom = async(values) => {
       try{
        const data = {
            ...values,
            idProduct: match.params.id
        }
        await ChapterApi.AddChapter(data);
        Swal.fire('...', 'Thm Thnh Cng!', 'success').then((result) => {
            if (result.isConfirmed) {
                setdemo(pre=>pre+1);
                history.push({ pathname: `/admin/products/Listchapter/${match.params.id}`})
            }
        })
       }catch(err){
           console.log(err);
          
       }


    }
    const handleRemoveChapter = (id)=>{
        try{
            const fetchRemoveChapter = async (data) => {
                try {
                    const res = await ChapterApi.DelChapter(data);
                    if (res.status == 200) {
                        Swal.fire('...', 'Xa Thnh Cng!', 'success').then((result) => {
                            if (result.isConfirmed) {
                                setdemo(pre=>pre+1);
                                history.push({ pathname: `/admin/products/Listchapter/${match.params.id}` })
                            }
                        })
                    }
                } catch (err) {
                    console.log(err);
                    Swal.fire('...', 'Khng  Thm quyn  xa', 'error').then((result) => {
                        if (result.isConfirmed) {
                            setdemo(pre=>pre+1);
                            history.push({ pathname: `/admin/products/Listchapter/${match.params.id}`})
                        }
                    })
                }
            }
    
            fetchRemoveChapter(id);
    
        }
        catch(err){
            console.log(err);
        }
    }

    const columns = [
        {
            title: 'S Th t',
            dataIndex: 'stt',
            key: 'stt',
        },
        {
            title: 'Tn chng',
            dataIndex: 'nameChapter',
            key: 'nameChapter',
        },
        {
            title: 'Ni dung chng',
            key: 'content',
            render: (record) => {
                return <>
                  <Button><Link to={`/admin/products/chapter/${record._id}`}>Chi tit</Link></Button>
                </>
            },
        },
        {
            title: 'Xa',
            key: 'del',
            render: (record) => (
              <Button onClick={()=>handleRemoveChapter(record._id)}>Xa</Button>
            ),
        },

    ];


    return (
        <div className="ChapterPage">
            <PageHeader
                className="site-page-header"
                onBack={() => window.history.back()}
                title="Danh sch chng"
            />

            <Row>
                <Col span={24}>
                    <div className="BoxForm">
                        <div className="title">Thm Chng</div>

                        <form onSubmit={forms.handleSubmit(handleSubmitFrom)} >

                            <div className="GroupForm">
                                <label htmlFor="nameChapter">Tn Chng</label>
                                <input name="nameChapter" ref={forms.register}></input>
                            </div>

                            <div className="GroupForm">
                                <label htmlFor="stt">S Th T</label>
                                <input name="stt" ref={forms.register}></input>
                            </div>
                            <Button htmlType='submit'>Lu</Button>
                        </form>
                    </div>
                </Col>
            </Row>

            <Row>
                <Col span={24}>
                    <Table dataSource={DataChap} columns={columns}></Table>
                </Col>
            </Row>
        </div>
    )
}

export default Chapter
