import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import adminApi from "../../../../api/adminApi";
import login from "../../../../Assets/Images/Admin/bg1.jpg";
import logout from "../../../../Assets/Images/Admin/bg2.jpg";
import Swal from "sweetalert2";
import { useForm } from "react-hook-form";
import BoxForgot from "./boxForgot";

const LoginAd = () => {
  const [show, setshow] = useState(false);
  const form = useForm();

  const toggleForm = () => {
    setshow(!show);
  };
  let history = useHistory();

  const setUserSession = (token, data) => {
    localStorage.setItem("token", token);
    localStorage.setItem("admin", JSON.stringify(data));
  };

  const handleLogin = async (values) => {
    try {
      const data = {};
      data.username = values.username;
      data.password = values.password;
      console.log(data);
      const res = await adminApi.LoginAdmin(data);
      console.log(res);
      Swal.fire(
        "ng Nhp Thnh Cng",
        `Cho Mng ${res.data.data[0].fullName} n vi trang qun tr `,
        "success"
      );
      setUserSession(res.data.token, res.data.data[0]);
      history.push("/admin");
      console.log(res.data.data[0].fullName);
    } catch (err) {
      Swal.fire(
        "ng Nhp Tht Bi",
        "Tn ng nhp hoc mt khu khng chnh xc vui lng kim tra li",
        "error"
      );
      console.log(err);
    }
  };

  return (
    <div className="BoxContent">
      <section>
        <div className={show ? "container active" : "container"}>
          <div className="user signinBx">
            <div className="imgBx">
              <img src={login} alt="" />
            </div>
            <div className="formBx">
              <form onSubmit={form.handleSubmit(handleLogin)}>
                <h2>ng Nhp</h2>
                <input
                  type="text"
                  name="username"
                  id
                  placeholder="Tn ti khon"
                  ref={form.register({
                    required: true,
                  })}
                />
                {form.errors.username && (
                  <p className="CatchError">
                    * Vui lng nhp cc k t trong bng ch ci
                  </p>
                )}
                <input
                  type="password"
                  name="password"
                  id
                  placeholder="Mt khu"
                  ref={form.register({ required: true })}
                />
                {form.errors.password && (
                  <p className="CatchError">* Vui lng nhp password</p>
                )}
                <button type="submit">ng Nhp</button>
                <button style={{background: "#d33"}} onClick={() =>  history.push('/')}>Quay li</button>

                <p className="signup">
                  Bn Qun Mt Khu?{" "}
                  <span onClick={toggleForm}>Ly Li Mt Khu.</span>
                </p>
              </form>
            </div>
          </div>

          <BoxForgot toggleForm={toggleForm} logout={logout} />
        </div>
      </section>
    </div>
  );
};

export default LoginAd;
