import { LoadingOutlined } from "@ant-design/icons";
import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { useHistory, useRouteMatch } from "react-router";
import Swal from "sweetalert2";
import adminApi from "../../../../api/adminApi";
import login from "../../../../Assets/Images/Admin/bg1.jpg";

const ResetPass = () => {
  const form = useForm();

  let history = useHistory();
  const match = useRouteMatch();

  const handleLogin = async (values) => {
    try {
      values.token = match.params.token;
      console.log(values);
      const res = await adminApi.NewPass(values);
      // console.log(res);
      Swal.fire("i mt khu thnh cng", `ng nhp  tip tc`, "success");
      history.push("/admins/login");
    } catch (err) {
      console.log(1);
      Swal.fire(
        "ng Nhp Tht Bi",
        "Tn ng nhp hoc mt khu khng chnh xc vui lng kim tra li",
        "error"
      );
      console.log(err);
    }
  };
  return (
    <div className="BoxContent">
      <section>
        <div className={"container"}>
          <div className="user signinBx">
            <div className="imgBx">
              <img src={login} alt="" />
            </div>
            <div className="formBx">
              <form onSubmit={form.handleSubmit(handleLogin)}>
                <h2>Nhp Mt Khu Mi</h2>
                <input
                  type="password"
                  name="password"
                  id
                  placeholder="Password"
                  ref={form.register({required: true,pattern:/^.*(?=.{8,})(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%&]).*$/})}
                />
                {form.errors.password && 
                    <p className="CatchError">
                        <p> Phi c t nht 8 k t</p>
                        <p> t nht 1 k t c bit t @ # $% </p>
                        <p> t nht 1 s, 1 ch thng, 1 ch hoa</p>
                    </p>
                }

                <button type="submit">i</button>
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default ResetPass;
