import React, { useEffect, useState } from 'react'
import Clock from './Components/Clock'

const Event = () => {
    const [timerDays, setTimerDays] = useState()
    const [timerHours, setTimerHours] = useState()
    const [timerMinutes, setTimerMinutes] = useState()
    const [timerSeconds, setTimerSeconds] = useState()
    const [dataBook, setdataBook] = useState()
    let interval 
    const startTimer = () => {
        const countDownDate = new Date("February 1, 2022").getTime();
        console.log(countDownDate);
        interval = setInterval(() => {
            const now = new Date().getTime()
            const distance = countDownDate - now 
            const days = Math.floor(distance / (24 * 60 * 60 * 1000))
            const hours = Math.floor(
                (distance % (24 * 60 * 60 * 1000)) / (1000 * 60 * 60)
            )
            const minutes = Math.floor(
                (distance % ( 60 * 60 * 1000)) / (1000 * 60)
            )
            const seconds = Math.floor(
                (distance % ( 60 * 1000)) / 1000
            )
            if(distance < 0){
                clearInterval(interval.current)
            }else {
                setTimerDays(days)
                setTimerHours(hours)
                setTimerMinutes(minutes)
                setTimerSeconds(seconds)
            }
        })
    }
    useEffect(() => {
        startTimer()
    })
    return (
        <>
            <div className="event_banner">
                <div className="event_banner_text">
                    <h6>Sch</h6>
                    <h2>Sch sp pht hnh - Ch gu Bc cc</h2>
                    <p>Cun sch l tp hp mi cu chuyn,
                        mi chuyn phiu lu ca Lars - mt ch gu trng b
                        sng  vng cc Bc. Vi tinh thn ham m khm ph, 
                        tm lng yu thng v s dng cm, Lars s l ngi bn
                        ng hnh thn thit, l tm gng sng cho cc bn c
                        nh tui.
                    </p>
                    <p>
                        <Clock
                            timerDays={timerDays}
                            timerHours={timerHours}
                            timerMinutes={timerMinutes}
                            timerSeconds={timerSeconds}
                        />
                    </p>
                </div>
            </div>
        </>
    )
}

export default Event
