import { SyncOutlined } from "@ant-design/icons";
import React, { useEffect } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import Swal from "sweetalert2";
import userApi from "../../../../api/userApi";

const Confirm = () => {
  const match = useRouteMatch();
  const history = useHistory();
  console.log(match.params.token);
  useEffect(() => {
    const fetchVerify = async (token) => {
      try {
        const data = {
          token: token,
        };
        const res = await userApi.verifyEmail(data);
        console.log(res.data.code);
        if (res.data.code === "200") {
          const dataLogin = {
            email: res.data.data.email,
            password: res.data.data.password,
          };
          console.log(dataLogin);
          const reslogin = await userApi.Login(dataLogin);
          if (reslogin.status === 200) {
            Swal.fire(
              "Xc Thc Thnh Cng",
              `Cho Mng   n vi chng ti `,
              "success"
            );
            localStorage.setItem("user-info", JSON.stringify(reslogin.data));
            localStorage.setItem("token", reslogin.data.token);
            console.log(reslogin);
            history.push("/account");
          }
        }
        if (res.data.code === "404") {
          Swal.fire(
            "Ti Khon  c Xc Thc Trc ",
            "error"
          );
          history.push("/login");
        }
      } catch (err) {
        console.log(err);
        Swal.fire(
          "Xc Thc Tht Bi",
          `Bn Xc Thc Email Tr Mt Ri :) cre jk`,
          "error"
        );
        history.push("/login");
      }
    };

    fetchVerify(match.params.token);
  }, []);
  return (
    <div className="confirm">
      <div className="error-container">
        <h2>ang Xc thc ti khon</h2>
        <SyncOutlined spin />
      </div>
    </div>
  );
};

export default Confirm;
