import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router';
import adminApi from '../../../../api/adminApi';
import login from '../../../../Assets/Images/Admin/bg1.jpg'
import logout from '../../../../Assets/Images/Admin/bg2.jpg'
import Swal from 'sweetalert2';
import { useForm } from 'react-hook-form';



const ChangePass = () => {
    const [show, setshow] = useState(false);
    const form = useForm();

    const toggleForm = () => {
        setshow(!show)
    }
    let history = useHistory();

    const setUserSession = (token, data) => {
        localStorage.setItem('token', token);
        localStorage.setItem('admin', JSON.stringify(data));
    }

    const handleLogin = async (values) => {
        try{
        const data ={};
        data.username=values.username;
        data.password=values.password;
        const res = await adminApi.LoginAdmin(data);
        Swal.fire('ng Nhp Thnh Cng', `Cho Mng ${res.data.data[0].fullName} n vi trang qun tr `, 'success');
        setUserSession(res.data.token, res.data.data[0]);
        history.push('/admin');
        console.log(res.data.data[0].fullName);
        }
        catch(err){
            console.log(1);
            Swal.fire('ng Nhp Tht Bi', 'Tn ng nhp hoc mt khu khng chnh xc vui lng kim tra li', 'error');
            console.log(err);
        }
    }

    const handleForgot = async (values)=>{
        try{
            const data={};
            data.email = values.email; 
            console.log(values);
             await adminApi.SendMail(data);
             Swal.fire({
                title: ' gi m xc thc vo email ca bn',
                text: "Bn c mun chuyn n trang Email ca mnh  xc thc ti khon khng ?",
                icon: 'success',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'chuyn n Email',
                cancelButtonText: ' li trang ny'
              }).then((result) => {
                if (result.isConfirmed) {
                  window.location="https://mail.google.com/mail/u/0/"
                }
              })
            // Swal.fire(' gi m xc thc', `vui lng vo hp mail ca mnh  xc thc ti khon `, 'success');
        }catch(err){
            Swal.fire('Email khng tn ti', 'error');
            console.log(err);
        }
    }

    const handleChangePass = async (values)=>{
        try{
            const admin = JSON.parse(localStorage.getItem("admin"))
            const data={};
            data.id = admin._id
            data.password = values.password; 
            data.newPassword = values.newPassword; 
            // console.log(data);
             await adminApi.ChangePass(data).then((res) => {
                if (res.status === 200) {
                    Swal.fire({
                        title : 'i mt khu thnh cng',
                        icon : 'success',
                    });
                    history.push("/admin");
                }
            })
        }catch(err){
                Swal.fire({
                title: 'Ngi qun tr khng tn ti hoc bn nhp sai mt khu!',
                icon: 'error',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Nhp li',
                cancelButtonText: 'Thot'
              }).then((result) => {
                if (!result.isConfirmed) {
                    history.push("/admin");
                }
              })
            // console.log(err);
        }
    }
    return (
        <div className="BoxContent">
            <section>
                <div className={show ? "container active" : "container"}>

                    {/* <div className="user signinBx">
                        <div className="imgBx"><img src={login} alt="" /></div>
                        <div className="formBx">
                            <form onSubmit={form.handleSubmit(handleLogin)} >
                                <h2>ng Nhp</h2>
                                <input type="text" name="username" id placeholder="Username" ref={form.register} />
                                <input type="password" name="password" id placeholder="Password" ref={form.register} />
                                <button type="submit">ng Nhp</button>
                                <p className="signup">Bn Qun Mt Khu? <span onClick={toggleForm}>Ly Li Mt Khu.</span></p>
                            </form>

                        </div>
                    </div> */}

                    <div className="user signinBx">
                        <div className="formBx">
                            <form onSubmit={form.handleSubmit(handleChangePass)}>
                                <h2>i Mt Khu</h2>
                                <input
                                    type="password"
                                    name="password"
                                    id
                                    placeholder="Mt khu c"
                                    ref={form.register({ required: true })}
                                    />
                                    {form.errors.password && (
                                    <p className="CatchError">* Vui lng nhp mt khu c</p>
                                    )}
                                     <input
                                    type="password"
                                    name="newPassword"
                                    id
                                    placeholder="Mt khu mi"
                                    ref={form.register({ required: true,pattern:/^.*(?=.{8,})(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%&]).*$/ })}
                                    />
                                    {form.errors.password && (
                                    <p className="CatchError">* Vui lng nhp mt khu an ton hn</p>
                                    )}
                                {/* <input type="email" name="email" id placeholder="Nhp Email" ref={form.register} /> */}
                                <button type="submit">Thay i</button>
                                <button style={{background: "#d33"}} onClick={() =>  history.push('/admin')}>Quay li</button>
                                {/* <p className="signup">Bn  C Ti Khon ? <span onClick={toggleForm}>ng Nhp.</span></p> */}
                            </form>

                        </div>
                        <div className="imgBx"><img src={logout} alt="" /></div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default ChangePass
