import { Button, Image, Table } from 'antd';
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from 'sweetalert2';
import prouctApi from '../../../../../api/productApi';

const ListProduct = (props) => {


  const handleDelProduct = async (id)=>{
    try {

      await prouctApi.DelProducts(id);

        Swal.fire('...', 'Xa Thnh Cng!', 'success').then((result) => {
                if (result.isConfirmed) {
                    props.handleRender();
                }
            })
    } catch (error) {
      console.log(error);
      Swal.fire('...', 'Xa Tht Bi!', 'error')
    }
  }

  const columns = [
    {
      title: 'Tn Sn Phm',
      dataIndex: 'nameProduct',
      key: 'nameProduct',
    },
    {
      title: 'hnh',
      dataIndex: 'images',
      key: 'images',
      render: (record) => {
        return <>
          <Image
            width={50}
            src={`https://beonlinelibrary.herokuapp.com/${record[0].image}`}
          />
        </>
      },
    },
    {
      title: 'Tc gi',
      dataIndex: 'author',
      key: 'author',
    },
    {
      title: 'Nh xut bn',
      dataIndex: 'nxb',
      key: 'nxb',
    },
    {
      title: 'gi',
      dataIndex: 'price',
      key: 'price',
    },
    {
      title: 'Chng',
      key: 'chapter',
      render: (record) => {
        return <>
          <Button><Link to={`/admin/products/Listchapter/${record._id}`}>Xem Chng ...</Link></Button>
        </>
      },
    },

    {
      title: 'sa',
      key: 'edit',
      render: (record) => {
        return <>
          <Button><Link to={`/admin/products/edit/${record._id}`}>Sa</Link></Button>
        </>
      },
    },

    {
      title: 'xa',
      key: 'edit',
      render: (record) => {
        return <>
          <Button onClick={() =>handleDelProduct(record._id)}>Xa</Button>
        </>
      },
    },
  ];

  return (
    <>
      <Table dataSource={props.data} columns={columns}/>;
    </>
  );
}



export default ListProduct