import { SyncOutlined } from "@ant-design/icons";
import React, { useEffect } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import Swal from "sweetalert2";
import orderApi from "../../../../api/orderApi";

const Confirm = () => {
  const match = useRouteMatch();
  const history = useHistory();
  console.log(match.params.token);
  useEffect(() => {
    const fetchVerify = async (token) => {
      try {
        const data = {
          token: token,
        };
        const res = await orderApi.verifyOrder(data);
        Swal.fire(
          "Xc Thc Thnh Cng",
          `Cm n bn  xc nhn n hng, Chng ti s c gng giao hng sm nht ti cho bn`,
          "success"
        );
        history.push(`/order-detail/${res.data.id}`);
      } catch (err) {
        console.log(err);
        Swal.fire(
          "Xc Thc Tht Bi",
          ` qu thi hn xc thc n hng chng ti rt tic phi bo vi bn n hng ca bn  b hy `,
          "error"
        );
        // history.push("/login");
      }
    };

    fetchVerify(match.params.token);
  }, []);
  return (
    <div className="confirm">
      <div className="error-container">
        <h2>ang thc hin vui lng ch...</h2>
        <SyncOutlined spin />
      </div>
    </div>
  );
};

export default Confirm;
